.listItem {
    width: 290px;
    margin: 10px;
}

.poster {
    border-radius: 8px;
    height: 430px;
    cursor: pointer;
    background-color: #fff;
    transition: all 300ms linear;
}

.poster:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 10px 5px grey;
}

.title {
    font-family: 'Cinzel', serif;
    display: block;
    font-size: 20px;
    color: black;
}